<style scoped lang='scss'>
#example {
  overflow: hidden;
  background: #bdbdbd; // animation:expand 0.4s ease;
}

#aside-bar,
#nav-bar {
  cursor: pointer;
}

#example svg {
  width: 100%;
  height: auto;
  display: block;
}

svg > g {
  transition: all 0.6s ease;
}

#nav-first,
#nav-second,
#nav-rect {
  transition: all 0.1s linear;
}

#example svg[nav="true"] > g {
  #nav-rect {
    transform: translateX(120px);
  }
  #nav-first > path {
    fill: #616161;
  }
  #nav-second > path {
    fill: #009688;
  }
}

#cloud {
  transform-origin: 50% 25%;
  animation: rotate 6s infinite;
} // #aside-mark{
//     transform: perspective(200px) rotate3d(1,0,0,50deg);
//     transform-origin:0px 100% 0;
// }
.in-skew {
  // animation: inSkew 0.5s linear;
}

@keyframes inSkew {
  0% {
    transform: translateY(0) rotate3d(1, 0, 0, 0deg) perspective(200);
  }
  50% {
    transform: translateY(50%) rotate3d(1, 0, 0, 90deg) perspective(200);
  }
  100% {
    transform: translateY(100%) rotate3d(1, 0, 0, 180deg) perspective(200);
  }
}

@keyframes rotate {
  0% {
    transform: scale(10) translateX(1000px);
    opacity: 0.2;
  }
  10% {
    transform: scale(1) translateX(500px);
    opacity: 0.3;
  }
  100% {
    transform: scale(0) translateX(0px);
    opacity: 0;
  }
}

@keyframes rotate-scale {
  0% {
    transform: rotate(0deg) scale(2);
  }
  100% {
    transform: rotate(360deg) scale(1.8);
  }
}

#star {
  transform-origin: 50% 50%;
  animation: rotate-scale 300s infinite;
}

svg {
  #background {
    transform: scale(0.8) rotate3d(1, 1, -0.29, 45deg)
      translate3d(20px, 150px, -50px);
  }
  #header {
    transform: scale(0.8) rotate3d(1, 1, -0.29, 45deg)
      translate3d(20px, 150px, 150px);
  }
  #table {
    transform: scale(0.8) rotate3d(1, 1, -0.29, 45deg)
      translate3d(20px, 150px, 20px);
  }
  #aside-bar {
    transform: scale(0.8) rotate3d(1, 1, -0.29, 45deg)
      translate3d(20px, 150px, 80px);
  }
  #nav-bar {
    transform: scale(0.8) rotate3d(1, 1, -0.29, 45deg)
      translate3d(20px, 150px, 220px);
  }
  #page-bar {
    transform: scale(0.8) rotate3d(1, 1, -0.29, 45deg)
      translate3d(20px, 150px, 100px);
  }
}

svg[state="1"],
svg:hover {
  #background {
    transform: scale(1);
  }
  #header {
    transform: scale(1);
  }
  #table {
    transform: scale(1);
  }
  #aside-bar {
    transform: scale(1);
  }
  #nav-bar {
    transform: scale(1);
  }
  #page-bar {
    transform: scale(1);
  }
}
</style>
<script>
export default {
  name: "role-list",
  data() {
    return {
      img: ""
    };
  },
  methods: {
    enterHdl(ev) {
      this.img.setAttribute("enter", true);
    },
    leaveHdl() {
      this.img.removeAttribute("enter");
    },
    navHdl(ev) {
      this.img.setAttribute("nav", true);
      setTimeout(() => {
        this.img.setAttribute("nav", false);
      }, 1200);
    },
    tableHdl(ev) {
      let target = ev.target;
      while (target.id !== "tableHdl") {
        target = target.parentNode;
      }
      target.removeAttribute("class");
      setTimeout(() => {
        target.classList.add("blink-list-right");
      }, 200);
    },
    asideHdl(ev) {
      var mark = document.getElementById("aside-mark");
      mark.classList.toggle("in-skew");
    }
  },
  mounted() {
    setTimeout(() => {
      this.img = document.getElementById("img");
      if (!img) return;
      this.img.removeAttribute("state");
    }, 400);
  }
};
</script>



