<template>
  <main>
    <transition name='slide'>
      <article v-show='seen'>
        <header class="sd-card">
          <section type='single'>
            <li class="ht-l">
              <button @click='backBtn' class="wd-l ht-l">
                <icon name="chevron-down"></icon>
              </button>
              <h1 id='project-name'>迭代编辑</h1>
              <button  v-if='cur==="update"&&isadmin' class="ht-l wd-l cl-alert" @click='confirmDel'>
                <icon name='trash'></icon>
              </button>
              <button class="ht-l wd-l" @click='save' v-if='cur==="update"&&isadmin'>
                <icon name='save'></icon>
              </button>
            </li>
          </section>
        </header>
        <content id='outer'>
          <section type='single'>
            <div class="sd-card">
              <nav-bar :navs='navs' :cur='cur' @nav='navHdl' type='inner'></nav-bar>
              <keep-alive> 
                <update v-if='cur==="update"' @refresh='init' ref='update' :proj='proj'></update>
                <staff v-if='cur==="staff"' @refresh='init' :proj='proj'></staff>
                <plan v-if='cur==="plan"' @refresh='init' :proj='proj'></plan>
              </keep-alive> 
            </div>
            <h4></h4>
          </section>
        </content>
      </article>
    </transition>
  </main>
</template>
<script>
import updateView from "./config.vue";
import planView from "./plan.vue";
import staffView from "./staff.vue";
export default {
  data() {
    return {
      proj: null,
      projName: '项目名称',
      seen: false,
      code: this.$route.query.code,
      navs: [{
        id: "update",
        name: "设置"
      }, {
        id: "staff",
        name: "人员"
      }, {
        id: "plan",
        name: "计划"
      }]
    };
  },
  methods: {
    backBtn() {
      this.$router.go(-1);
    },
    navHdl(cur) {
      this.cur = cur;
    },
    save() {
      this.$refs.update.save()
    },
    del() {
      this.$http
        .post("/api/projects/remove", { code: this.proj.code })
        .then(rep => {
          if (!rep.body) return false;
          this.$store.commit("toast", "删除成功");
          setTimeout(time => {
            this.$router.push("/project/list");
          }, 2000);
        });
    },
    confirmDel() {
      this.$store.commit("setConfirm", {
        seen: true,
        msg: "确认删除项目？",
        out: res => {
          if (res) {
            this.del();
          }
        }
      });
    },
    init(){
    this.seen = true;
    this.$http.post("/api/projects/findOne", { code: this.code })
      .then(res => {
        var data = res.body.data
        if (res.body.code !== 0) {
          return this.$store.commit("toast", "网络不给力");
        }
        data.start = new Date(data.start).Format("yyyy-MM-dd");
        data.end = new Date(data.end).Format("yyyy-MM-dd");
        this.projName = data.name;
        this.proj = data;
      });
  },
  },
  updated() {
    document.getElementById("outer").scrollTop = 0;
  },
  mounted() {
    this.init()
  },
  components: {
    plan: planView,
    staff: staffView,
    update: updateView
  },
  computed: {
    isadmin() {
      return this.$store.state.isadmin
    },
    cur: {
      get() {
        return this.$store.state.projEdit;
      },
      set(val) {
        this.$store.commit("setProjEdit", val);
      }
    },
  }
};

</script>



